/*********************
CUSTOM
*********************/
.prepend-top {
  margin-top: 1.3em;
}
.append-bottom {
  margin-bottom: 1.3em;
}
/*********************
NOTES TIMELINE
*********************/
#notes-timeline span.date {
  top: 4px;
}
#notes-timeline span.time {
  top: 20px;
}
#notes-timeline span.date,
#notes-timeline span.time {
  font-size: 11px;
  position: absolute;
  color: #6B6B6B;
  left: 0;
  width: 70px;
  text-align: right;
}
#notes-timeline article {
  min-height: 48px;
  position: relative;
  padding-left: 85px;
}
#notes-timeline .timeline-content {
  min-height: 48px;
  border-left: 1px solid #555;
  padding-left: 17px;
  padding-bottom: 15px;
  position: relative;
}
#notes-timeline .timeline-content i {
  font-size: 18px;
  height: 25px;
  line-height: 25px;
  background-color: #FFF;
  width: 12px;
  text-align: center;
  position: absolute;
  left: -8px;
  top: 0;
}
#notes-timeline article:last-child .timeline-content {
  border-left: none;
  padding-bottom: 0;
}
/*********************
TODO TIMELINE
*********************/
#todo-timeline .todo-container {
  margin-bottom: 1.3em;
  border: 1px solid #DDD;
  padding: 15px;
}
